import React, { Component } from 'react';
import jsCookie from "js-cookie";
import config from '../../utils/config';
import utils from '../../utils/utils';
import { get } from 'axios';
import { get as getValue } from 'lodash-es';
import Timer from '../../utils/Timer';
import { addBrigeCallback,removeBrigeCallback } from "../../utils/brigeForNative";
import {
    TWO_FACTOR_HELPER,
    RECEIVE_CODE_FROM,
    SEND_TEXT_MESSGE,
    USE_BACKUP_CODE_DESC,
    USE_BACKUP_CODE,
    COMPELETE,
    GET_VOICE_CODE,
    OPERATION_TOO_FREQUENT,
    OPERATION_OVER_RUN,
} from '../../config/copywriting';
import { SEND_ONE_TIME_PASSWORD } from '../../config/interfaceUrl';

import Separator from '../../components/Separator';
import BasicButton from '../../components/BasicButton';
import confirm from '../../components/Confirm/Confirm';

import styles from './index.module.scss';

let isNetRequesting = false;//网络请求防抖
let speechSessionId = '';
let account = ''; // 账户信息
const from = utils.getQueryParams('from');
const inMac = from === 'mac';

const timer = new Timer();
const {BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME} = config;
const GET_VERCODE_ERROR_TOO_OFTEN="otp.too.often.error";
const GET_VERCODE_ERROR_LIMIT_REACHED="otp.limit.reached.error";

export default class RegistrationSmsHelpComp extends Component {

    constructor(props) {
        super(props);
        this.state = {
            inMac:false,
            vercodeBtnUseable: true,
        }
    }

    postMessageToMac(action,params){
        try {
            inMac && window.webkit.messageHandlers[action].postMessage(params);
        } catch(err) {
            console.log(err);
        }
    }
    remainingTimeCallback(remainingTime){
        console.log(remainingTime);
        timer.startTimer({
            count:remainingTime,
            stepCallback(time){
                console.log(time);
            }
        })
    }

    componentDidMount() {
        account = '18592074084'; //jsCookie.get('registrationSpeechAccount');
        this.setState({
            inMac
        });
        // utils.setWindowTitle(UNABLE_TO_GET_VERIFICATION);
        addBrigeCallback(BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME,this.remainingTimeCallback);
        this.postMessageToMac('remainingTime',{callback:BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME});
    }

    /**
     * 关闭本页
     */
    closeCurPage(registrationType) {
        window.opener = null;
        window.open('', '_self');
        window.close();
        this.postMessageToMac('closeWindow','');
    }


    /**
     * 获取语音/短信验证码
     */
    getVerCodeByType(type = 'voiceotp'){
        const _this = this;
        //验证码必须有合法手机号
        if (!account) {
            return;
        }

         //防抖
        if (isNetRequesting) {
            return;
        }
        isNetRequesting = true;

        let recipient = account;
        let sig;
        let params = {
            recipient,
            [type]: '',
        };
        if(speechSessionId){
            params = Object.assign({}, params, {
                speechSessionId,
            })
        }
        sig = utils.getSignByParam(params);

        get(SEND_ONE_TIME_PASSWORD, {
            params: {sig,...params}
        }).then((data) => {
            const sessionId = getValue(data, 'data.sessionId');
            const errorCode = getValue(data, 'data.errors[0].code');
            if (sessionId) {
                speechSessionId = sessionId;
                jsCookie.set('speechSessionId', sessionId ,{expires:1/24/6}); //把获取到的验证码存到 cookie 内,验证码十分钟过期
            }else{
                if (errorCode) {
                    switch (errorCode) {
                        case GET_VERCODE_ERROR_TOO_OFTEN:
                            confirm({ content: OPERATION_TOO_FREQUENT });
                            break;
                        case GET_VERCODE_ERROR_LIMIT_REACHED:
                            confirm({ content: OPERATION_OVER_RUN });
                            break;
                        default:
                            break;
                    }
                }
            }
        }).catch(function (error) {
            console.log(error);
        }).finally(function () {
            isNetRequesting = false;
            _this.goBack();
        });
    }

    componentWillUnmount(){
        removeBrigeCallback(BRIGE_CALLBACK_NAME_FOR_GET_REMAINING_TIME,this.remainingTimeCallback);
    }

    goBack = () => {
        this.props.history.go(-1);
    }

    render() {
        return (
            <div className={styles.twoFactorHelper}>
                <div className={styles.textWrapper}>
                    <div className={styles.title}>
                        {TWO_FACTOR_HELPER}
                    </div>
                    <Separator pure className={styles.titleSeparator}/>
                    <div className={styles.content}>
                        <div className={styles.block}>
                            <div className={styles.item}>
                                <div className={styles.order}>1. </div>
                                <div>{RECEIVE_CODE_FROM}</div>
                            </div>
                            <BasicButton
                                text={SEND_TEXT_MESSGE}
                                buttonWrapperClass={styles.messageBtn}
                                onClick={this.getVerCodeByType.bind(this, 'textotp')}
                            />
                            <BasicButton
                                text={GET_VOICE_CODE}
                                buttonWrapperClass={styles.messageBtn}
                                onClick={this.getVerCodeByType.bind(this, 'voiceotp')}
                            />
                        </div>
                        <Separator pure className={styles.messageSeparator} />
                        <div className={styles.block}>
                            <div className={styles.item}>
                                <div className={styles.order}>2. </div>
                                <div>{USE_BACKUP_CODE_DESC}</div>
                            </div>
                        </div>
                    </div>
                </div>
                <BasicButton
                    text={USE_BACKUP_CODE}
                    buttonWrapperClass={styles.useBackupBtn}
                    onClick={this.goBack}
                />
                <Separator pure className={styles.backupSeparator}/>
                <BasicButton
                    text={COMPELETE}
                    buttonWrapperClass={styles.completeBtn}
                    onClick={this.goBack}
                />
            </div>
        )
    }
}
